import React, { Component } from "react";
import PropTypes from "prop-types";

import InputMask from 'react-input-mask';

import st from "./style.js";
import Colors from "../../style/Colors";

class TextInput extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      text: this.props.initialText,
      length: this.props.initialText.length,
      last_length: null,
    };
    this.inputRef = React.createRef();
  }

  onChange = ($arg) => {

    let _text = $arg.target.value;

    this.setState(prevState => ({
      text: _text,
      last_length: prevState.length,
      length: _text.length,
    }));
    
    this.props.onChange(_text);

    setTimeout(()=>{
      this.checkCompleteInput();  
    })
    
  }

  onKeyPress = ($arg) => {
    if($arg.key === 'Enter' && 
    this.state.length>=this.props.minLenght){
      this.props.onSubmit(this.state.text);
    }
  }

  checkCompleteInput = () => {

    if(!this.props.minLenght)return;
    if (
      this.state.length < this.props.minLenght &&
      this.state.last_length >= this.props.minLenght
    ) {
      this.props.onResetInput(this.state.text);
    }
    if (
      this.state.length === this.props.minLenght &&
      this.state.last_length < this.props.minLenght
    ) {
      this.props.onCompleteInput(this.state.text);
    }
  }

  focus = () => {
    this.inputRef.focus();
  }

  blur = () => {
    this.inputRef.blur();
  }

  render() {

    return (
      <div style={{...st.container(this.props),...this.props.style}}>
        <p style={{...st.placeholder(this.props),...this.props.placeholderStyle}}>
          {
            !this.state.text?this.props.placeholder:''
          }
        </p>
    
        <InputMask 
          inputRef={ref => {this.inputRef=ref}}
          mask={this.props.mask}
          maskChar=""
          style={st.input(this.props)} 
          //placeholder={this.props.placeholder}
          value={this.state.text}
          onChange={text => this.onChange(text)}
          onKeyPress={this.onKeyPress}
          onFocus={this.props.onFocus}
          onBlur={this.props.onBlur}
        />

      </div>
    );
  }
}

TextInput.propTypes = {
  placeholder: PropTypes.string,
  onChange:PropTypes.func,
  onCompleteInput:PropTypes.func,
  onResetInput:PropTypes.func,
  onSubmit: PropTypes.func,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  initialText:PropTypes.string,
  mask:PropTypes.string, // 'ex 99.999-99'
  borderColor: PropTypes.string,
  borderWidth:PropTypes.number,
  minLenght:PropTypes.number,
  textColor: PropTypes.string,
  inputStyle: PropTypes.oneOf(['Default', 'Success', 'Warning', 'Error']),
  placeholderStyle: PropTypes.object,
  style:PropTypes.object,
};
TextInput.defaultProps = {
  placeholder: '',
  onChange:()=>{},
  onCompleteInput:()=>{},
  onResetInput:()=>{},
  onSubmit:()=>{},
  onFocus:()=>{},
  onBlur:()=>{},
  initialText:'',
  mask:"",
  borderColor: Colors.lightGrey10,
  borderWidth:2,
  minLenght: null,
  textColor: Colors.darkGrey20,
  inputStyle:   'Default',//Default | Success | Warning | Error
  placeholderStyle: {},
  style:{},
};

export default TextInput;
