import Colors from "../../style/Colors";

const getSuccessOrErrorColor = ($props) => {
  if($props.inputStyle === "Error"){
    return Colors.error;
  }
  if($props.inputStyle === "Success"){
    return Colors.success;
  }
  return null;
}

export default  {
  container:$props=>({
    border: 0,
    // borderBottom: `${$props.borderWidth}px solid ${$props.borderColor}`,
    borderBottomWidth: $props.borderWidth,
    borderBottomStyle: 'solid',
    borderBottomColor: getSuccessOrErrorColor($props) || $props.borderColor,
    display: 'flex',
    flexDirection: 'column',
    flex: 1,
    position: 'relative',
  }),
  placeholder:$props=>({
    position: 'absolute',
    top: 0,
    left: 0,
    margin: 0,
    border: 0,
    padding: 0,
    marginLeft: 2,
    marginTop: 1,
    fontSize: 18,
    fontFamily: 'Muli-Regular',
    color: getSuccessOrErrorColor($props) || $props.textColor,
    whiteSpace: 'nowrap',
  }),
  input:$props=>({
    position: 'relative',
    paddingLeft: 2,
    flex: 1,
    width: '100%',
    marginBottom: 20,
    fontSize: 18,
    fontFamily: 'Muli-Regular',
    color: getSuccessOrErrorColor($props) || $props.textColor,
    border: 0,
    backgroundColor: 'rgba(0,0,0,0)',
    outline: 'none',
  }),
};

